.popular-goods {
  &__content {
    margin: 25px 0 35px;
  }

  &__title {
    font-size: 25px;
    line-height: 30px;
    @extend %Barlow-Bold;
    margin-bottom: 25px;
  }

  &__menu-list {
    font-size: 15px;
    line-height: 18px;
    font-weight: normal;
    justify-content: start;
  }

  &__item {
    margin-right: 50px;

    a {
      padding-bottom: 5px;
    }
  }

  &__item:last-child {
    margin-right: 0;
  }

  &__slider-item {
    box-sizing: border-box;
  }

  &__slider-navigation {
    position: relative;
    width: 1300px;
    right: 70px;
    bottom: 210px;
  }


}

@media (max-width: 480px) {
  .popular-goods {
    &__item {
      padding-bottom: 20px;
    }
  }
}



