.banner__price--oldprice::before {
  content: '';
  width: 49px;
  height: 2px;
  position: absolute;
  top: 7px;
  background-color: #C4C4C4;
  transform: rotate(-12deg);
}

.banner__item-title {
  font-size: 18px;
  line-height: 21px;
  padding: 5px 0 7px;
}

.banner__footer {
  background-color: #F0F0F4;
  bottom: 0;
  width: 100%;
  height: 100px;
  text-align: center;
  padding-top: 26px;
}

.banner__text {
  font-size: 18px;
  line-height: 21px;
}

.banner__text--blue {
  font-size: 18px;
  line-height: 21px;
  color: #1C62CD;
}

.tabs__item--active {
  border-bottom: 3px solid #1c62cd;
}

.tabs__content-item {
  display: none;
}

.tabs__content-item--active {
  display: block;
}

.product__item {
  text-align: center;
  width: 270px;
  height: 403px;
  border: 1px solid #E0E0E0;
  padding: 52px 35px 30px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: width .5s linear;
}

.product__item--line {
  width: 100%;
  transition: width .5s linear;
}

.product__item--line .product__quick-view {
  right: auto;
}

.product__title {
  font-size: 18px;
  line-height: 21px;
  margin-bottom: 15px;
}

.product__title a {
  text-decoration: none;
}

.product__price {
  font-size: 30px;
  line-height: 36px;
}

.product__btn {
  width: 60px;
  height: 40px;
  background: #1c62cd;
  border-radius: 10px 0 0 0;
  padding-top: 10px;
  position: absolute;
  right: 0;
  bottom: 0;
  cursor: pointer;
  border: 0;
  outline: none;
}

.product__btn-icon {
  width: 30px;
  height: 22px;
  fill: #fff;
}

.product__favorites {
  position: absolute;
  top: 16px;
  right: 18px;
  border: 0;
  background: no-repeat;
  cursor: pointer;
}

.product__favorites-icon {
  width: 22px;
  height: 22px;
}

.product__favorites--active .product__favorites-icon {
  fill: #ff0000;
}

.product__quick-view {
  width: 230px;
  height: 61px;
  background: #FFFFFF;
  opacity: 0.9;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  display: none;
  position: absolute;
  top: 120px;
  right: 17px;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  padding-top: 15px;
}

.product__img-link:hover .product__quick-view {
  display: block;
}

.product__not-available {
  width: 100%;
  height: 90px;
  position: absolute;
  z-index: 2;
  background-color: #fff;
  bottom: 0;
  right: 0;
  display: none;
}

.product__not-available p {
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  padding-bottom: 13px;
}

.product__not-available a {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #1C62CD;
}

.product__not-available--active {
  display: block;
}

.breadcrumbs {
  font-size: 15px;
  line-height: 18px;
  margin: 42px 0;
}

.breadcrumbs__list-item {
  display: inline-block;
}

.breadcrumbs__list-item::after {
  content: ">";
  color: #C4C4C4;
  margin: 0 10px;
}

.breadcrumbs__list-item:last-child::after {
  display: none;
}

.breadcrumbs__link-item {
  text-decoration: none;
  color: #C4C4C4;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.pagination__item {
  margin-right: 27px;
  padding: 5px 10px;
  color: #C4C4C4;
  font-size: 18px;
  line-height: 22px;
}

.pagination__item-active {
  border: 2px solid #1C62CD;
  color: #1C62CD;
}

.products-filter__form {
  display: flex;
  flex-direction: column;
}

.products-filter__tabs-list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 48px;
}

.products-filter__tab-item {
  text-transform: uppercase;
  margin-right: 25px;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.09em;
}

.products-filter__drop-item-title {
  position: relative;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  padding-left: 23px;
  margin-bottom: 25px;
  cursor: pointer;
}

.products-filter__drop-item-title::before {
  position: absolute;
  content: '';
  background-image: url("../img/svg/sprite/sprite.svg#arrow-down-filter");
  width: 12px;
  height: 12px;
  left: 0;
  top: 3px;
  opacity: .3;
  transform: rotate(-90deg);
  transition: all .3s;
}

.products-filter__drop-item-title--active::before {
  transform: rotate(0deg);
}

.products-filter__content {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0;
}

.products-filter__content .irs.irs--round.js-irs-0 {
  position: relative;
  top: -30px;
}

.products-filter__content-box {
  font-size: 13px;
  line-height: 17px;
  margin: 0 auto;
}

.products-filter__content-box:first-child {
  margin-left: 0;
}

.products-filter__content-box--full-width {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin: 0 0 20px;
}

.products-filter__content-box--full-width label {
  margin-right: auto;
}

.products-filter .jq-checkbox {
  background: #fff;
  width: 14px;
  height: 14px;
  border: 1px solid #C4C4C4;
  border-radius: 1px;
  box-shadow: none;
  vertical-align: -1px;
  margin: 0 8px 0 0;
}

.products-filter .jq-checkbox.checked {
  background: #1C62CD;
  border: none;
}

.products-filter .jq-checkbox.checked .jq-checkbox__div {
  width: 10px;
  height: 5px;
  margin: 3px 0 0 2px;
  border-bottom: 1px solid #fff;
  border-left: 1px solid #fff;
  transform: rotate(-50deg);
}

.products-filter .irs--round.irs-with-grid {
  width: 100%;
}

.products-filter .irs,
.products-filter .irs-line {
  width: 100%;
}

.products-filter .irs--round .irs-handle {
  background-color: #006cfa;
  width: 8px;
  height: 8px;
  top: 34px;
}

.products-filter .irs-min,
.products-filter .irs-max {
  margin-top: 60px;
}

.products-filter__range-controls {
  position: relative;
  top: -10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  line-height: 17px;
}

.products-filter__range-controls label {
  width: 80px;
}

.products-filter__range-controls input {
  width: 50px;
  border: none;
  border-bottom: 1px solid #2F3035;
  outline: none;
}

.products-filter__option-item {
  display: flex;
  margin-bottom: 26px;
}

.products-filter__option-item-title {
  font-size: 15px;
  line-height: 19px;
  padding-bottom: 6px;
  width: 268px;
  border-bottom: 2px solid #C4C4C4;
}

.products-filter__option-item-list {
  position: relative;
  right: 60px;
  bottom: 10px;
}

.products-filter__option-item-list .jq-selectbox__select {
  font-size: 14px;
  line-height: 40px;
  border: none;
  background: none;
  box-shadow: none;
  padding: 0 34px 0 10px;
}

.products-filter__option-item-list .jq-selectbox__trigger {
  border-left: none;
}

.products-filter__option-item-list .jq-selectbox__trigger-arrow {
  top: 17px;
}

.products-filter__option-item-list .jq-selectbox.opened .jq-selectbox__trigger-arrow {
  transform: rotate(180deg);
}

.products-filter__option-item-list .jq-selectbox.focused .jq-selectbox__select {
  border: none;
}

.products-filter__input-model {
  width: 291px;
  height: 30px;
  border: 1px solid #E0E0E0;
  padding: 8px 20px 11px;
  margin-bottom: 25px;
}

.products-filter__input-model::placeholder {
  font-size: 12px;
  line-height: 14px;
  color: #BDBEC2;
}

.products-filter__submit-btn {
  width: 252px;
  height: 50px;
  font-size: 15px;
  line-height: 18px;
  text-align: center;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  cursor: pointer;
}

.products-filter__reset-btn {
  display: inline-block;
  width: 131px;
  align-self: center;
  font-size: 13px;
  line-height: 16px;
  text-decoration-line: underline;
  margin: 15px auto;
}

.products-filter__sticker {
  display: inline-block;
  height: 25px;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.07em;
  padding: 6px 15px;
  margin: 0 auto;
  color: #FFFFFF;
}

.products-filter__sticker--sale {
  background: #1C62CD;
}

.products-filter__sticker--disabled {
  background: #F0F0F4;
}

.products-filter__sticker--dealer {
  background: #2F3035;
}

.humburger__btn {
  display: none;
  border: none;
  background: transparent;
}

.mobile-menu__list {
  display: none;
  position: absolute;
  width: 300px;
  background: #FFFFFF;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  z-index: 99;
  font-size: 20px;
  line-height: 24px;
  font-weight: normal;
  padding-left: 10px;
}

.mobile-menu__list li {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 36px;
  padding-left: 46px;
