}

.mobile-menu__icon {
  width: 20px;
  height: 20px;
  position: absolute;
  left: 0;
  bottom: 9px;
}

.product-suggestion__content {
  margin: 25px 0 35px;
}

.product-suggestion__title {
  font-size: 25px;
  line-height: 30px;
  margin-bottom: 25px;
}

.product-suggestion__menu-list {
  font-size: 15px;
  line-height: 18px;
  font-weight: normal;
  justify-content: start;
}

.product-suggestion__item {
  margin-right: 50px;
}

.product-suggestion__item a {
  padding-bottom: 5px;
}

.product-suggestion__item:last-child {
  margin-right: 0;
}

.product-suggestion__slider-item {
  box-sizing: border-box;
