.catalog__products-list {
  display: flex;
  width: 885px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: auto;
}

.catalog__title {
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 30px;
}

.catalog__properties-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.catalog__properties-item {
  display: inline-block;
  padding: 4px 22px;
  background-color: #F0F0F4;
  border-radius: 3px;
  font-size: 14px;
  line-height: 17px;
  opacity: 0.7;
}

.catalog__view-btn {
  opacity: .4;
  cursor: pointer;
}

.catalog__view-btn svg {
  stroke: #2F3035;
}

.catalog__view-btn-line {
  width: 25px;
  height: 19px;
  margin-bottom: 1px;
  stroke-width: 17px;
}

.catalog__view-btn-grid {
  width: 23px;
  height: 21px;
  fill: none;
  stroke-width: 2px;
}

.catalog__view-btn--active {
  opacity: 1;
}

.catalog__btn-mobile-icon {
  display: none;
  stroke: #2f3035;
  stroke-width: 2px;
  width: 21px;
  height: 21px;
}

.catalog__view-btn--filter {
  opacity: 1;
}

.product-page__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.product-page__description {
  width: 570px;
}

.product-page__description-menu {
  height: 30px;
  margin: 15px 0;
}

.product-page__description-menu a {
  padding: 5px 0;
}

.product-page__table {
  width: 100%;
}

.product-page__table td {
  padding: 15px 30px 6px 0;
}

.product-page__table tr {
  border-bottom: 1px solid rgba(47, 48, 53, 0.1);
}

.product-page__vendor-id {
  font-size: 15px;
  line-height: 18px;
  opacity: 0.6;
}

.product-page__description-content {
  margin-bottom: 45px;
}

.product-page__title-description {
  font-size: 30px;
  line-height: 36px;
  padding-bottom: 10px;
}

.product-page__menu-details {
  height: 70px;
  background: #F0F0F4;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
}

.product-page__menu-details a {
  padding: 10px 0;
}

.product-page__details-content {
  font-size: 16px;
  line-height: 150%;
  padding: 44px 0 100px 10px;
}

.product-page__price {
  text-align: center;
}

.product-page__price--current {
  font-size: 40px;
  line-height: 48px;
}

.product-page__price--old {
  display: inline-block;
  font-size: 30px;
  line-height: 36px;
  opacity: 0.9;
  position: relative;
  margin-bottom: 12px;
}

.product-page__price--old::before {
  content: "";
  width: 160px;
  height: 2px;
  position: absolute;
  top: 15px;
  background-color: #c4c4c4;
  transform: rotate(-12deg);
}

.product-page__description-buttons {
  padding: 25px 0 50px;
}

.product-page__description-buttons .product__favorites {
  display: inline-block;
  position: static;
}

.product-page__description-buttons .jq-ry-container {
  display: inline-block;
}

.product-page__buy-button {
  width: 177px;
  height: 50px;
  text-align: center;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  background: #1C62CD;
}

.product-page__buy-button--small {
  width: 140px;
  height: 35px;
}

.product-page__search-shop {
  padding-bottom: 50px;
  width: 960px;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.product-page__search-shop h3 {
  font-size: 17px;
  line-height: 20px;
  opacity: 0.7;
  display: inline;
}

.product-page__search-field {
  width: 313px;
  height: 33px;
  background: #F0F0F4;
  border: none;
  border-radius: 3px;
  position: relative;
  background-image: url("../img/svg/product-page__search-field.svg");
  background-repeat: no-repeat;
  background-position: right 10px center;
  margin: 0 100px 0 17px;
}

.product-page__table-pickup th {
  padding-bottom: 40px;
}

.product-page__table-pickup td {
  width: 220px;
  font-size: 16px;
  line-height: 150%;
  opacity: 0.7;
}

.product-page__table-pickup td:first-child {
  width: 270px;
}

.product-page__table-pickup td:last-child {
  text-align: right;
  padding-right: 0;
  opacity: 1;
}

/* footer section */

.footer {
  width: 100%;
  height: 232px;
  background-color: #F0F0F4;
  padding: 45px 0 27px;
}

.footer__top {
  display: flex;
  justify-content: space-between;
  font-style: normal;
  font-size: 14px;
  line-height: 17px;
}

.footer__subscription {
  width: 278px;
}

.footer__menu-title {
  background-color: transparent;
  margin-bottom: 5px;
}

.footer__menu-item {
  margin-bottom: 5px;
}

.footer__menu-item a {
  text-decoration: none;
}

.footer__bottom {
  font-size: 10px;
  line-height: 12px;
}

.footer__form {
  display: flex;
  margin: 34px 0;
}

.footer__form-input {
  width: 186px;
  height: 33px;
  border: none;
}

.footer__form-button {
  width: 109px;
  height: 33px;
  background: #1c62cd;
  color: #fff;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: .12em;
  text-transform: uppercase;
  border: none;
  padding: 0;
}

.footer__bottom {
  margin-top: 15px;
  display: flex;
  justify-content: center;
}

.footer__bottom a:first-child {
  margin-right: 80px;
}

.footer__social {
  display: flex;
  align-items: center;
}

.footer__social svg {
  margin-right: 30px;
}

@media (max-width: 1350px) {
  .new-items__slider-navigation {
    width: 95%;
    right: 0;
    left: 5px;
  }

  .new-items__slider-navigation {
    width: 100%;
    right: 0;
    left: 0;
  }
}

@media (max-width: 1200px) {
  .header__top .container,
  .header__bottom .container {
    max-width: 100%;
    padding: 0;
  }

  .header__top .container {
    padding: 0 10px;
  }

  .promo__wrapper {
    justify-content: space-around;
  }

  .promo__slider {
    width: 800px;
  }
}

@media (max-width: 1199px) {
  .container {
    max-width: 960px;
  }
}

@media (max-width: 1024px) {
  .promo__banner {
    display: none;
  }

  .promo__wrapper {
    justify-content: center;
  }

  .promo__slider {
    width: 768px;
  }

  .catalog__btn-mobile-icon {
    display: inline-block;
  }

  .catalog__aside {
    display: none;
  }

  .catalog__products-list {
    justify-content: space-around;
  }

  .product-page__description-menu li {
    white-space: nowrap;
  }

  .product-page__description-menu li:first-child {
    margin-right: 20px;
  }
}

@media (max-width: 991px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 960px) {
  .header-menu__item:not(:first-child) {
    padding-left: 25px;
  }

  .user-menu__list {
    display: flex;
  }

  .header .menu-categories__list {
    font-size: 16px;
  }

  .header__adress {
    width: 200px;
    margin-left: 10px;
  }

  .categories__item {
    width: 360px;
  }

  .footer__top {
    flex-wrap: wrap;
  }

  .footer__bottom {
    justify-content: flex-end;
  }
}

@media (max-width: 768px) {
  .search__tabs {
    display: flex;
    overflow-x: scroll;
  }

  .search__tabs-item {
    white-space: nowrap;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: 100%;
    margin: 0 auto;
  }

  .menu-categories__list {
    flex-wrap: wrap;
  }

  .product-suggestion__title {
    margin-bottom: 55px;
  }

  .categories__item {
    width: 300px;
    margin: 10px 20px;
  }

  .categories__item-image {
    max-width: 100px;
    height: auto;
  }

  .categories__inner {
    justify-content: center;
  }

  .product-page__menu-details {
    display: flex;
    align-items: center;
    height: 130px;
  }

  .product-page__description-content,
  .product-page__description-menu {
    display: none;
  }

  .product-page .menu-categories__item {
    padding-bottom: 20px;
  }

  .product-page__menu-list {
    width: 480px;
    margin: 0 auto;
  }

  .product-page__menu-list li {
    margin-bottom: 10px;
  }

  .footer {
    height: auto;
  }

  .footer .product__favorites-icon:last-child {
    margin-right: 0;
  }
}

@media (max-width: 760px) {
  .header-menu__list {
    display: none;
  }

  .humburger__icon {
    width: 33.67px;
    height: 27px;
  }

  .humburger__btn {
    display: inline-block;
  }

  .header__adress {
    display: none;
  }

  .header__bottom-inner .menu-categories {
    display: none;
  }
}

@media (max-width: 480px) {
  .container {
    max-width: auto;
  }

  .header-menu__list {
    display: none;
  }

  .menu-categories {
    height: 90px;
  }

  .menu-categories__list {
    flex-wrap: wrap;
  }

  .products-filter {
    display: none;
  }

  .mobile-menu__list.is-active {
    display: flex;
    flex-direction: column;
  }

  .product-suggestion__item {
    padding-bottom: 20px;
  }

  .promo {
    margin-top: 25px;
  }

  .promo__slider {
    width: 370px;
  }

  .search__tabs {
    display: flex;
    overflow-x: scroll;
  }

  .search__tabs-item {
    white-space: nowrap;
  }

  .categories__inner {
    justify-content: center;
  }

  .categories__item {
    width: 288px;
    min-width: 288px;
    padding-right: 13px;
  }

  .categories__item-image {
    max-width: 100px;
    height: auto;
  }

  .popular-goods__item {
    padding-bottom: 20px;
  }

  .new-items__item {
    padding-bottom: 20px;
  }

  .catalog__properties-wrapper {
    flex-wrap: wrap;
  }

  .catalog__sorting {
    order: 1;
    margin-bottom: 10px;
  }

  .catalog__properties {
    order: 2;
  }

  .catalog__products-list {
    justify-content: center;
  }

  .product-page__main,
  .product-page__top {
    display: flex;
    flex-direction: column;
  }

  .product-page__menu-details {
    background: none;
    margin: 120px 0;
    display: block;
    padding: 120px 0;
  }

  .product-page__menu-details .menu-categories__item {
    padding: 15px 0;
  }

  .product-page__menu-list {
    display: flex;
    flex-direction: column;
    width: 360px;
  }

  .product-page__description-content,
  .product-page__description-menu {
    display: none;
  }

  .product-page__description {
    width: auto;
    order: 1;
  }

  .product-page__appearance {
    order: 2;
  }

  .product-page__search-shop {
    flex-direction: column;
    width: 100%;
  }

  .product-page__search-field {
    margin: 10px 0;
  }

  .product-page__table-pickup {
    display: flex;
  }

  .product-page__table-pickup tr {
    border: 0;
  }

  .product-page__table-pickup th {
    margin-right: 10px;
  }

  .product-page__table-pickup th,
  .product-page__table-pickup td {
    padding: 0;
    padding-bottom: 30px;
    border: 0;
  }

  .product-page__table-pickup thead tr {
    display: flex;
    flex-direction: column;
  }

  .product-page__table-pickup tbody tr {
    display: flex;
    flex-direction: column;
  }

  .product-page__table-pickup tbody tr:not(:first-child) {
    display: none;
  }

  .product-page__table-pickup td:first-child {
    width: auto;
  }

  .footer {
    height: auto;
    background-color: #fff;
  }

  .footer .container {
    padding: 0;
  }

  .footer__top {
    flex-direction: column;
    background-color: #F9F9FC;
  }

  .footer__subscription {
    padding: 17px 10px 0;
  }

  .footer__information-menu .footer__menu-list,
  .footer__shop-menu .footer__menu-list {
    display: none;
  }

  .footer__information-menu .footer__menu-title,
  .footer__shop-menu .footer__menu-title {
    border-top: none;
  }

  .footer__menu-title {
    padding: 16px;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #7F7F7F;
    border-top: 2px solid #F0F0F4;
    border-bottom: 2px solid #F0F0F4;
    margin-bottom: 0;
  }

  .footer__social {
    padding-top: 40px;
    padding-bottom: 100px;
    background-color: #fff;
    justify-content: center;
  }

  .footer__bottom {
    flex-direction: column;
  }

  .footer__bottom a:first-child {
    margin-bottom: 12px;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .promo__slider {
    width: 100%;
  }
}